.selection-form__fieldset {
  .margin();
  padding: 0;
  border: none;

  &--top {
    margin-top: -62px;
  }
}

.selection-form__legend {
  display: block;
  margin-bottom: 35px;
  .header-selection();
  padding-top: 30px;

  &-padding {
    padding-bottom: 22px;
  }

  &::after {
    position: absolute;
    content: "";
    background-color: @green-button;
    margin-left: 43px;
    margin-top: -5px;
    width: 20vw;
    height: 3px;
  }
}

.selection-form__legend--middle {
  &::after {
    position: absolute;
    content: "";
    background-color: @green-button;
    margin-top: 12px;
    margin-left: 26px;
    width: 29vw;
    height: 3px;
  }
}

.selection-form__button {
  .green-button();
  .margin();
  outline: none;
  border: none;
  padding-top: 0;
  font-size: 16px;
}

.selection-form__fieldset ::placeholder {
  .upper(16px);
  color: @greybrown;
  font-family: "Oswald", "Arial", sans-serif;
}

.selection-form__required {
  &--bottom {
    .arial();
    .center();
    line-height: 1.14;
    margin-top: 8px;
    margin-bottom: 30px;
  }
}

.selection-form__wrapper {
  margin-bottom: 50px;
}

@media (min-width: @tablet-width) {
  .selection-form__fieldset-info {
    margin-bottom: 18px;
  }

  .selection-form__legend {
    position: relative;
    margin-left: 48px;

    font-size: 36px;
    font-weight: normal;

    &::placeholder {
      font-size: 20px;
    }

    &--middle {
      top: 35px;
      margin-bottom: 90px;
    }

    &::before {
      position: absolute;
      content: "";
      background-color: @green-button;
      height: 3px;
      margin-top: 20px;
      left: -250px;
      width: 210px;
    }

    &::after {
      position: absolute;
      content: "";
      background-color: @green-button;
      margin-left: 40px;
      margin-top: 20px;
      width: 60vw;
      height: 3px;
    }
  }

  .selection-form__button {
    height: 52px;
    font-size: 20px;
    margin-left: 70px;
    margin-bottom: 80px;
  }

  .selection-form__wrapper {
    margin-top: 30px;
    margin-left: 50px;

    &--goal {
      margin-bottom: 110px;
    }
  }

  .selection-form__case {
    display: flex;
  }

  .selection-form__required {
    &--bottom {
      display: block;
      margin-top: 20px;
      margin-left: 50px;
      font-size: 16px;
    }
  }
}

@media (min-width: @desktop-width) {
  .selection-form__container {
    display: flex;
    margin-bottom: 43px;
  }

  .selection-form__fieldset {
    width: 570px;
    height: 195px;
    margin-right: 0;
    margin-left: 0;
    margin-bottom: 90px;

    &--top {
      margin-bottom: 60px;
    }

    &--bottom {
      margin-top: 160px;
      margin-bottom: 0;
    }
  }

  .selection-form__fieldset-info {
    width: auto;
    min-height: 197px;
    margin-right: 80px;
  }

  .selection-form__button {
    width: 570px;
    margin-left: 110px;
    margin-right: 80px;
    margin-top: 60px;
    margin-bottom: 0;
  }

  .selection-form__case {
    margin-bottom: 70px;
  }

  .selection-form__required {
    &--bottom {
      margin-left: 10px;
      margin-top: 80px;
    }
  }

  .selection-form__wrapper {
    width: 570px;
    margin-left: 110px;
    margin-top: 35px;
    margin-bottom: 10px;

    &--goal {
      margin: 0;
      margin-top: 80px;
      margin-left: 20px;
    }
  }

  .selection-form__legend {
    margin-left: 110px;

    &-padding {
      margin-top: 0;
    }

    &--middle {
      margin-top: 0;
      margin-bottom: 0;
    }

    &--middle::after {
      width: 1100px;
    }
  }
}
