.page-social {
  font-family: "Oswald", "Arial", sans-serif;
  background-color: @grey;
  min-height: 240px;
}

.page-social__icons {
  display: flex;
  align-items: center;
  width: 280px;
  min-height: 60px;
  margin: auto;
  border-top: 1px solid @greybrown;
  border-bottom: 1px solid @greybrown;
}

.page-social__link {
  .center();
  display: block;
  padding: 0;
  margin: 0;
  position: relative;
  margin-top: 20px;
  margin-bottom: 30px;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.page-social__svg {
  display: block;
  margin-top: -7px;
  margin-right: 5px;
  width: 40px;
  height: 47px;
}

.page-social__icon path:hover {
  fill: @green-button;
}

.page-social__icon path:active {
  fill: @green-button;
  opacity: 0.3;
}

.page-social__center {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 125px;
  margin: 0 auto;
}

@media (min-width: @tablet-width) {
  .page-social {
    display: flex;
    align-items: center;
    padding-left: 35px;
    padding-right: 10px;
    min-height: 150px;
    box-sizing: border-box;
  }

  .page-social__icons {
    display: flex;
    align-items: center;
    border: none;
    margin-left: 70px;
  }

  .page-social__icon path {
    &:hover {
      fill: @green-button;
    }
  }

  .page-social__icon--vk {
    width: 30px;
    height: 16px;
  }

  .page-social__icon--inst {
    width: 23px;
    height: 23px;
  }

  .page-social__icon--fb {
    width: 14px;
    height: 28px;
  }

  .page-social__academy {
    margin-right: 10px;
  }
}

@media (min-width: @desktop-width) {
  .page-social {
    display: flex;
    min-height: 150px;
    width: 1440px;
  }

  .page-social__academy {
    margin-left: 80px;
    margin-right: -40px;
  }

  .page-social__icons {
    margin-left: 330px;
  }
}
